import { useState } from 'react';
import { Layout, Button, Form, Input, message, Select } from 'antd';
import { ethers, utils } from 'ethers';
import Wallet from './components/Wallet';
import { erc1155Contract, erc721Contract } from './utils/contracts/contract';
import './App.css';

function App() {
    const { Header, Content } = Layout;
    const [tokenType] = useState("1");
    const [amount] = useState(1);

    const handlerErc1155Transfer = async (trans_data, signer) => {
        const account = await signer.getAddress();

        try {
            const contra_instance = erc1155Contract(trans_data.nft_token, signer);
            await contra_instance.safeTransferFrom(
                account,
                trans_data.receive,
                trans_data.token_id,
                1,
                '0x'
            );
        } catch (error) {
            console.log(error);
        }
    }

    const handlerErc721Transfer = async (trans_data, signer) => {
        const account = await signer.getAddress();

        try {
            const contra_instance = erc721Contract(trans_data.nft_token, signer);
            await contra_instance.transferFrom(
                account,
                trans_data.receive,
                trans_data.token_id
            );
        } catch (error) {
            console.log(error);
        }
    }

    const onFinish = async (values) => {
        const provider = new ethers.providers.Web3Provider(window.ethereum)

        if (provider) {
            if (!utils.isAddress(values.nft_token)) {
                message.error("合约地址格式不正确");
                return;
            } else {
                const code_res = await provider.getCode(values.nft_token);
                if (code_res === '0x') {
                    message.error("无效的合约地址");
                    return;
                }
            }

            if (!utils.isAddress(values.receive)) {
                message.error("接收地址格式不正确");
                return;
            }

            const signer = provider.getSigner();
            if (values.token_type == 1) {
                await handlerErc721Transfer(values, signer);
            } else {
                await handlerErc1155Transfer(values, signer);
            }
        } else {
            message.error("连接Metamask失败");
        }
    };

    return (
        <div className="Container">
            <Layout>
                <Header>
                    <div className='ConnectWalletR'>
                        <Wallet />
                    </div>
                </Header>
                <Content className='Container-Cont'>
                    <Form
                        name="basic"
                        labelCol={{ span: 4 }}
                        wrapperCol={{ span: 16 }}
                        initialValues={{ remember: true }}
                        onFinish={onFinish}
                        autoComplete="off"
                    >
                        <Form.Item
                            label="合约地址"
                            name="nft_token"
                            rules={[{ required: true, message: '填写转帐的NFT合约地址' }]}
                        >
                            <Input placeholder='填写转帐的NFT合约地址' />
                        </Form.Item>

                        <Form.Item
                            label="接收地址"
                            name="receive"
                            rules={[{ required: true, message: '填写需要接收NFT的地址' }]}
                        >
                            <Input placeholder='填写需要接收NFT的地址' />
                        </Form.Item>

                        <Form.Item label="代币类型" name="token_type" initialValue={tokenType}>
                            <Select>
                                <Select.Option value="1">ERC-721</Select.Option>
                                <Select.Option value="2">ERC-1155</Select.Option>
                            </Select>
                        </Form.Item>

                        <Form.Item
                            label="TokenID"
                            name="token_id"
                            rules={[{ required: true, message: '填写需要转出的代币ID' }]}
                        >
                            <Input placeholder='填写需要转出的代币ID' />
                        </Form.Item>

                        <Form.Item
                            label="数量"
                            name="amount"
                            initialValue={amount}
                            rules={[{ required: true, message: '填写需要转出的代币数量' }]}
                        >
                            <Input placeholder='填写需要转出的代币数量' />
                        </Form.Item>

                        <Form.Item wrapperCol={{ offset: 8, span: 16 }}>
                            <Button type="primary" htmlType="submit">
                                确认转帐
                            </Button>
                        </Form.Item>
                    </Form>
                </Content>
            </Layout>
        </div>
    );
}

export default App;
